<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
function getDay() {
  let full = new Date();
  return full.toLocaleDateString();
}

export default {
  name: "protocolOrgManagementComponent",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      form: {
        today: getDay(), // 建立日期
        PO_NAME: "", // 組織名稱
        MANAGER: "", // 負責人
        Location_Id: [], // 廠區
      },
      loading: false,
      rules: {
        PO_NAME: [{ required: true, message: "必填", trigger: "blur" }],
        MANAGER: [{ required: true, message: "必填", trigger: "change" }],
        Location_Id: [{ required: true, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "po_Name",
          label: "組織名稱",
          width: "250",
        },
        {
          id: "create_Date",
          label: "建立日期",
          width: "250",
        },
        {
          id: "managerInfo",
          label: "負責人姓名/工號/分機",
          width: "250",
        },
        {
          id: "col4",
          label: "廠區數/會員數",
          width: "",
        },
      ],
      areaQryEmp: [],
      tableData: [],
      tableList: [],
      locations: [],
    };
  },
  computed: {},
  methods: {
    memberManagementHandler(item) {
      this.$router.push({
        path: "/memberManagement",
        query: {
          data: JSON.stringify(item),
        },
      });
    },
    editProtocolOrg(item) {
      this.$router.push({
        path: "/editProtocolOrg",
        query: {
          data: JSON.stringify(item),
        },
      });
    },
    remoteSelectManagerHandler(evt) {
      this.loading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.loading = false;
        if (!evt.target.value) return;
        this.getAreaQryEmp(evt.target.value);
      }
    },
    visibleHandleChange(e) {
      if (!e) {
        this.areaQryEmp = [];
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const res = await this.$store.dispatch("insProtocalOrg", this.form);
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "申請成功",
            });
            this.resetForm(formName);
            this.qryFacAttendList();
            this.$store.state.currentPage = 1;
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    async dialogDeleteHandler(id) {
      const res = await this.$store.dispatch("delProtocalOrg", id);
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "刪除成功",
        });
        this.qryFacAttendList();
        this.$store.state.currentPage = 1;
      }
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    async qryFacAttendList() {
      const res = await this.$store.dispatch("qryFacAttendList");
      const data = res.data.data;
      this.locations = data.locations;
      this.tableData = JSON.parse(JSON.stringify(data.facList));
      this.tableData.forEach((e, index) => {
        e.index = index + 1;
        e.managerInfo = `${e.name}/${e.manager}/${e.ext_No || ""}`;
        e.col4 = `${e.po_Count}/${e.member_Count}`;
      });
      this.tableList = this.tableData.slice(0, 10);
    },
    async getAreaQryEmp(word) {
      const res = await this.$store.dispatch("protocalAreaQryEmp", word);
      const data = res.data.data;
      this.areaQryEmp = data;
    },
  },
  async mounted() {
    this.$store.state.currentPage = 1;
    await this.qryFacAttendList();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./protocolOrgManagement.html"></template>
